import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Camera, CameraResultType , CameraSource } from '@capacitor/camera';
import { ModalController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { ToastService } from 'src/app/services/toasts/toast.service';
import { ModalPage } from '../modal/modal.page';

@Component({
  selector: 'app-transfer-ownership',
  templateUrl: './transfer-ownership.page.html',
  styleUrls: ['./transfer-ownership.page.scss'],
})
export class TransferOwnershipPage implements OnInit {

  public show : any = 'false';

  public addt_ownership : any = 
    {
      b_id:'',
      p_id:'',
      customer_id:'',
      t_customer_id:'',
     t_total_payment:'', 
     t_advance:'', 
     t_total_installment:'', 
     t_remaining_payment:'', 
     t_granter_name:'', 
     t_granter_mobileno:'', 
     t_granter_cnic:'', 
     t_granter_cnic_img:'',
     t_witness_name:'',
     t_witness_mobileno:'',
     t_witness_cnic:'',
     t_witness_cnic_img:''
    }
  allinstallments: any;
  Plot_Data: any;
  Customer_Data: any;
  selected_plot: any;
 public selected_customer: any = 0;
  public p_no: any ;
  public lastluqmaninstallments: any = 0 ;
  public last7starinstallments: any = 0 ;
  public installment_by_id: any ;
  public amounttotal: any ;
  public advanceAmount: any ;
  public Category: any = 'View all installment';
  detail: any;
  public selectTabs:any = 'Baba Fareed';

  constructor( public route : Router , public apicall : ApicallService , public toast : ToastService , public global : GlobalService, public modal : ModalController ) { }

  ngOnInit() {
    this.get_Plot();
    this.get_Customer();
  }

  async get_bookedplots($event){
    console.log($event.target.value)
    await this.apicall.api_getbooked_plots($event.target.value);
    await this.global.BookedProperties.subscribe( res => {
      this.p_no = res
    })
    
    console.log(this.p_no[0].p_id)
    await this.apicall.api_getlast_InstallmentByp_id(this.p_no[0].p_id);
    this.get_last_install();
  }

  async get_last_install(){
    if(this.selectTabs == 'Baba Fareed'){
      this.global.LumanTownInstallments.subscribe(res => {
        console.log(res);
        this.lastluqmaninstallments = res[0];  
        console.log(this.lastluqmaninstallments);
        this.addt_ownership.b_id = this.lastluqmaninstallments.b_id;
        this.addt_ownership.p_id = this.lastluqmaninstallments.p_id;
        this.addt_ownership.customer_id = this.lastluqmaninstallments.customer_id;
         console.log(this.lastluqmaninstallments);
         console.log(this.addt_ownership.customer_id);
        console.log('Baba Fareed');
       });
    }
    // if(this.selectTabs == '7 Star'){
    //   this.global._7Starinstallments.subscribe(res => {
    //     console.log(res);
    //     this.last7starinstallments = res[0];  
    //     console.log(this.last7starinstallments);
    //     this.addt_ownership.b_id = this.last7starinstallments.b_id;
    //     this.addt_ownership.p_id = this.last7starinstallments.p_id;
    //     this.addt_ownership.customer_id = this.last7starinstallments.customer_id;
    //      console.log(this.last7starinstallments);
    //      console.log(this.addt_ownership.customer_id);
    //     console.log('7 Star');
    //    });
    // }
    else{
      console.log('no plots');
    }
  }


  

  async view_all_installment(id) {
    

    await this.apicall.api_getInstallmentByp_id(id)
    this.global.Getinstallmentbyid.subscribe(res => {
      console.log(res);
      this.installment_by_id = res; 
     });

     
   const modal = await this.modal.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      initialBreakpoint: 0.93 ,
      breakpoints: [0, 0.435, 0.93],
      componentProps: {
        'Category' : this.Category,
        'Installment_details' : this.installment_by_id,
      }
    });
    console.log('modal')
    return await modal.present();
  }

  async firstwitness_cnic
  (){
    const image = await Camera.getPhoto({
      quality:90,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt,
      allowEditing: false
    });
    document.getElementById('cameraImage1').setAttribute('src', `data:image/${image.format};base64,`+image.base64String );
      console.log(image.base64String);
      this.addt_ownership.t_granter_cnic_img = image.base64String;
  }

  async secondwitness_cnic(){
    const image = await Camera.getPhoto({
      quality:90,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt,
      allowEditing: true
    });
    document.getElementById('cameraImage2').setAttribute('src', `data:image/${image.format};base64,`+image.base64String );
      console.log(image.base64String);
      this.addt_ownership.t_witness_cnic_img =  image.base64String;      
      
  }

  Property(){
    this.route.navigate(['property']);
  }
  Plot(){
    this.route.navigate(['plots']);
  }
  T_ownership(){
    this.route.navigate(['transfer-ownership']);
  }
  Home(){
    this.route.navigate(['home']);
  }

  async get_Plot() {
    this.apicall.api_getPlots();
     this.global.Plots.subscribe(user => {
      console.log(user);
      this.Plot_Data = user;        
     });
   }

   async get_Customer() {
    this.apicall.api_get_customer();
    await this.global.Customer.subscribe(data => {
      console.log(data);
      this.Customer_Data = data;
     });
   }

  async select_plot($event){
    console.log($event.target.value); 
    this.apicall.api_get_plot_by_id($event.target.value); 
    this.global.Get_plot_byid.subscribe(res =>{
      console.log(res)
      this.selected_plot = res;
      this.show ='true';
    } )
  }

  async select_customer($event){
    console.log($event.target.value); 
   await this.apicall.api_get_customer_by_id($event.target.value); 
    this.global.Get_customer_byid.subscribe(res =>{
      console.log(res);
      this.selected_customer = res[0];
      console.log(this.selected_customer);
      this.addt_ownership.t_customer_id = this.selected_customer.customer_id;
      console.log(this.addt_ownership.t_customer_id);
    } );
  }

  totalamount($event){
    this.amounttotal = $event.target.value;
      console.log(this.amounttotal);
      console.log($event.target.value);
  }
  advanceamount($event){
    this.advanceAmount = $event.target.value;
      console.log(this.advanceAmount);
      this.addt_ownership.t_remaining_payment = this.amounttotal - this.advanceAmount;
      console.log(this.addt_ownership.t_remaining_payment);
  }
  
 async transfer_plot(){    
    console.log(this.addt_ownership)
   await this.apicall.api_insert_transfer(this.addt_ownership);
   document.getElementById('cameraImage1').setAttribute('src', '');
   document.getElementById('cameraImage2').setAttribute('src', '');
  this.addt_ownership = 
  {
    b_id:'',
    p_id:'',
    customer_id:'',
    t_customer_id:'',
   t_total_payment:'', 
   t_advance:'', 
   t_total_installment:'', 
   t_remaining_payment:'', 
   t_granter_name:'', 
   t_granter_mobileno:'', 
   t_granter_cnic:'', 
   t_granter_cnic_img:'',
   t_witness_name:'',
   t_witness_mobileno:'',
   t_witness_cnic:'',
   t_witness_cnic_img:''
  }
  }



}

